@import "../../../global.scss";

.new{
    display: flex;
    .newContainer{
        flex: 6;
        .top,.buttom{
            -webkit-box-shadow:2px 4px 10px 1px rgba(0,0,0,0.47);
            box-shadow: 2px 4px 10px 1px rgba(201,201,201,0.47);
            padding: 10px;
            margin: 20px;
            display: flex;
            h1{
                color: $maincolor;
            }
            .left{
                flex: 1;
                text-align: center;
                img{
                    width: 100px;
                    height: 100px;
                    border-radius: 50%;
                    object-fit: cover;
                }
            }
            .right{
                flex: 2;
                form{
                    display: flex;
                    flex-wrap: wrap;
                    gap: 20px;
                    justify-content:safe;
                    .userInput{
                        width: 40%;
                        label{
                            display: flex;
                            align-items: center;
                            gap: 10px;

                        }
                        input{
                            width: 100%;
                            padding: 7px;
                            border: none;
                            outline: none;
                            border-bottom: 1px solid gray;
                            &:hover{
                                font-weight: bold;
                            }
                        }
                    }
                    button{
                        width: 150px;
                        padding: 10px;
                        border: none;
                        outline: none;
                        background-color: teal;
                        color: white;
                        font-weight: bold;
                        cursor: pointer;
                        border-radius: 5px;
                        &:hover{
                            color: darkblue;
                            background-color: lightblue;
                            transition: all 0.5s ease;
                        }
                    }
                }
            }
        }
    }
}