@import "../../../global.scss";

.home{
    display: flex;
    .homecontainer{
        flex: 6;

        .widgets,.charts{
            display: flex;
            padding: 20px;
            gap: 20px;
        }
        .charts{
            padding:5px 20px;
        }
        .listContainer{
            -webkit-box-shadow:2px 4px 10px 1px rgba(0,0,0,0.47);
            box-shadow: 2px 4px 10px 1px rgba(201,201,201,0.47);
            margin: 20px;
            padding: 20px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .listTitle{
                color: gray;
                margin-bottom: 15px;
                font-weight: 500;
            }
        }
    }
}